<template>
    <v-dialog
            v-model="show" :fullscreen="$vuetify.breakpoint.smAndDown" width="600px" origin="center top">
        <v-card>
            <v-card-title>
                <span>Agregar Servicio</span>
            </v-card-title>
            <v-card-text>
                <ServiceForm @Service="close">
                    <v-btn  rounded @click="show = false" text color="error" class="mr-2">Cancelar</v-btn>
                </ServiceForm>
            </v-card-text>
        </v-card>
    </v-dialog>
</template>

<script>
    import ServiceForm from "../_frm/FrmService";
    
    export default {
        name: 'DlgService',
        components: {ServiceForm},
        props: {
            value: Boolean,
        },
        data: () => ({

        }),
        methods: {
            close($event) {
                this.$emit('Service', $event);
                this.show = false;
            }
        },
        computed: {
            show: {
                get () {
                    return this.value
                },
                set (value) {
                    this.$emit('input', value)
                }
            }
        }
    }
</script>

