<template>
  <div class="pa-3">
    <div class="d-flex justify-space-between align-center mb-5">
      <h2>
        <v-icon class="mr-2">mdi-handshake</v-icon>Servicios
        <v-btn outlined color="primary" @click="dialog = true">
          <v-icon class="mr-2">mdi-plus</v-icon>
          Nuevo</v-btn
        >
      </h2>
    </div>
    <!--Dialogo de agregar servicios-->
    <DlgService v-model="dialog" @Service="getServices" />
    <DlgEditService
      v-model="dialogEdit"
      :form="selectedService"
      v-if="selectedService"
      @service="getServices"
    />

    <v-row v-if="services.length > 0" dense>
      <v-col cols="12" lg="12" md="12">
        <v-card shaped outlined>
          <v-card-text>
            <v-row align="center" justify="center">
              <v-col cols="9">
                <div v-for="(s, i) in services" :key="s._id">
                  <v-expansion-panels v-if="i == 0" v-model="xpanel">
                    <v-expansion-panel>
                      <v-expansion-panel-header color="primary">
                        <h3
                          align="center"
                          justify="center"
                          style="
                            color: white;
                            text-align: left;
                            margin-left: 0px;
                          "
                        >
                          <v-icon dense dark>mdi-check-all</v-icon>
                          Servicio {{ i + 1 }}
                        </h3>
                      </v-expansion-panel-header>
                      <v-expansion-panel-content>
                         <v-container class="mt-5">
                          <v-row align="center" justify="center">
                            <v-col cols="12" lg="3" md="3" sm="12">
                              <v-text-field
                                v-model="s.name"
                                label="Nombre"
                                outlined
                                shaped
                                placeholder="Nombre del servicio"
                                required
                                disabled
                              />
                            </v-col>

                            <v-col cols="12" lg="6" md="6" sm="12">
                              <v-textarea
                                v-model="s.description"
                                placeholder="Descripción del servicio"
                                icon="mdi-information"
                                label="Descripción"
                                rounded="rounded"
                                shaped
                                outlined

                                disabled
                              ></v-textarea>
                            </v-col>
                            <v-col cols="2" lg="2" md="2" sm="2">
                              <div class="text-center">
                                <v-tooltip bottom>
                                  <template v-slot:activator="{ on, attrs }">
                                    <v-btn
                                      class="ma-2"
                                      dark
                                      v-bind="attrs"
                                      v-on="on"
                                      small
                                      outlined
                                      fab
                                      color="teal"
                                      @click="editService(s)"
                                    >
                                      <v-icon>mdi-pencil</v-icon>
                                    </v-btn>
                                  </template>
                                  <span>Editar</span>
                                </v-tooltip>
                                <v-tooltip bottom>
                                  <template v-slot:activator="{ on, attrs }">
                                    <v-btn
                                      class="ma-2"
                                      dark
                                      v-bind="attrs"
                                      v-on="on"
                                      small
                                      outlined
                                      fab
                                      color="red"
                                      @click="deleteService(s)"
                                    >
                                      <v-icon>mdi-delete</v-icon>
                                    </v-btn>
                                  </template>
                                  <span>Eliminar</span>
                                </v-tooltip>
                              </div>
                            </v-col>
                          </v-row>
                        </v-container>

                        <v-row>
                          <v-container>
                            <v-row align="center">
                              <v-col cols="12" lg="12" md="12">
                                <Subservice
                                  :subservice="subservice[i]"
                                ></Subservice>
                              </v-col>
                            </v-row>
                          </v-container>
                        </v-row>
                      </v-expansion-panel-content>
                    </v-expansion-panel>
                  </v-expansion-panels>

                  <v-expansion-panels v-else v-model="s.id">
                    <v-expansion-panel>
                      <v-expansion-panel-header color="primary">
                        <h3
                          align="center"
                          justify="center"
                          style="
                            color: white;
                            text-align: left;
                            margin-left: 0px;
                          "
                        >
                          <v-icon dense dark>mdi-check-all</v-icon>
                          Servicio {{ i + 1 }}
                        </h3>
                        <v-icon></v-icon>
                      </v-expansion-panel-header>
                      <v-expansion-panel-content>
                        <v-container class="mt-5">
                          <v-row align="center" justify="center">
                            <v-col cols="3" lg="3" md="3" sm="12">
                              <v-text-field
                                v-model="s.name"
                                label="Nombre"
                                outlined
                                shaped
                                placeholder="Nombre del servicio"
                                required
                                disabled
                              />
                            </v-col>

                            <v-col cols="6" lg="6" md="6" sm="12">
                              <v-textarea
                                v-model="s.description"
                                placeholder="Descripción del servicio"
                                icon="mdi-information"
                                label="Descripción"
                                rounded="rounded"
                                shaped
                                outlined
                                disabled
                              ></v-textarea>
                            </v-col>
                            <v-col cols="2" lg="2" md="2" sm="2">
                              <div class="text-center">
                                <v-tooltip bottom>
                                  <template v-slot:activator="{ on, attrs }">
                                    <v-btn
                                      class="ma-2"
                                      dark
                                      v-bind="attrs"
                                      v-on="on"
                                      small
                                      outlined
                                      fab
                                      color="teal"
                                      @click="editService(s)"
                                    >
                                      <v-icon>mdi-pencil</v-icon>
                                    </v-btn>
                                  </template>
                                  <span>Editar</span>
                                </v-tooltip>
                                <v-tooltip bottom>
                                  <template v-slot:activator="{ on, attrs }">
                                    <v-btn
                                      class="ma-2"
                                      dark
                                      v-bind="attrs"
                                      v-on="on"
                                      small
                                      outlined
                                      fab
                                      color="red"
                                      @click="deleteService(s)"
                                    >
                                      <v-icon>mdi-delete</v-icon>
                                    </v-btn>
                                  </template>
                                  <span>Eliminar</span>
                                </v-tooltip>
                              </div>
                            </v-col>
                          </v-row>
                        </v-container>

                        <v-row>
                          <v-container>
                            <v-row align="center" justify="center">
                              <v-col cols="12" lg="12" md="12">
                                <Subservice
                                  :subservice="subservice[i]"
                                ></Subservice>
                              </v-col>
                            </v-row>
                          </v-container>
                        </v-row>
                      </v-expansion-panel-content>
                    </v-expansion-panel>
                  </v-expansion-panels>
                  <hr class="mb-3" />
                </div>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col md="12">
        <div class="text-center pt-2">
          <v-pagination
            v-model="page"
            :length="pageCount"
            @input="getServices"
          ></v-pagination>
        </div>
      </v-col>

      <v-row v-if="services.length === 0 && !loader">
        <v-col md="12" class="text-center">
          Aun no se han creado sesiones, Agregue una desde el botón "Nueva"
        </v-col>
      </v-row>
    </v-row>

    <Loader :show="loader" message="Cargando servicios...."></Loader>

    <Confirm ref="confirm"></Confirm>
  </div>
</template>

<script>
import DlgService from "./_dialogs/DlgService";
import DlgEditService from "./_dialogs/DlgEditService";
import Subservice from "./Subservice";
import Loader from "../../System/Loader";
import Confirm from "../../System/Confirm";
export default {
  components: { Confirm, Loader, DlgEditService, DlgService, Subservice },
  data: () => ({
    dialog: false,
    dialogEdit: false,
    loader: false,
    xpanel: 0,
    services: [],
    service: [],
subservice: [],
    subarray: [],
    headers: [
      { text: "Orden del dia", value: "text" },
      { text: "Opciones", value: "action", align: "right", sortable: false },
    ],
    search: "",
    page: 1,
    pageCount: 1,
    itemsPerPage: 5,
    selectedService: null,
  }),

  created() {
    this.page = 1;
    this.getServices();
  },
  methods: {
    getServices() {
      this.loader = true;
      this.$http
        .get(`/listar-servicios/${this.page}`)
        .then((res) => {
          this.services = res.data.services;
          console.log(this.services)
          this.datagen(res.data.services);
        })
        .finally(() => (this.loader = false));
    },
    datagen(data) {
      var cont1 = data.length;
      for (let index = 0; index < cont1; index++) {
        var cont2 = data[index].optservices.length;
        const macroarray = []; 
        for (let index2 = 0; index2 < cont2; index2++) {
          const subarray = { name, id, description };
          var id = data[index].optservices[index2]._id;
          var name = data[index].optservices[index2].name;
          var description = data[index].optservices[index2].description;
          subarray.id = id;
          subarray.name = name;
          subarray.description = description;
          macroarray[index2] = subarray;
        }
        this.subservice[index] = macroarray;
      }
    }, 
    editService(service) {
      this.selectedService = {
        ...service,
        text: service.text,
      };
      this.dialogEdit = true;
    },
    async deleteService(service) {
      const confirm = await this.$refs.confirm.open(
        "Eliminar Servicio",
        `¿Esta seguro que desea eliminar este servicio?`
      );
      if (!confirm) return;
      this.$http
        .delete(`/eliminar-servicio/${service._id}`)
        .then((res) => {
          if (res.data && res.data.status === "success") {
            const index = this.services.indexOf(service);
            this.services.splice(index, 1);
            this.$alert.ok(res.data.message);
          } else {
            this.$alert.err("Upps! algo salió mal reintenta");
          }
        })
        .catch((err) => () => {
          if (err.response && err.response.data) {
            this.$alert.err(err.response.data.message);
          }
        });
    },
  },
};
</script>

<style scoped>
.service-link {
  text-decoration: none;
  text-transform: uppercase;
  color: #6a6a6b;
}
</style>
