<template>
  <v-card>
    <v-card-title
      class="white--text pa-1"
      style="margin-top: 5px; background: #4d7ca6"
    >
      <v-icon dark>mdi-checkbox-marked-circle-outline</v-icon>
      <span style="margin-left: 10px">Sub-Servicios</span>
    </v-card-title>
    <v-row class="pa-4 mb-5" justify="space-between">
      <v-col cols="5">
        <v-treeview
          :active.sync="active"
          :items="items"
          :open.sync="open"
          activatable
          color="primary"
          open-on-click
          transition
        >
          <template v-slot:prepend="{ item }">
            <v-icon v-if="!item.children"> mdi-check </v-icon>
          </template>
        </v-treeview>
      </v-col>
      <v-divider vertical></v-divider>

      <v-col class="d-flex text-center">
        <v-scroll-y-transition mode="out-in">
          <div
            v-if="!selected"
            class="title grey--text text--lighten-1 font-weight-light"
            style="align-self: center"
          >
            Seleccionar un sub-servicio
          </div>
          <v-card
            v-else
            :key="selected._id"
            class="pt-6 mx-auto"
            flat
            max-width="400"
          >
       <div align="center">
              <v-card width="180px" height="180px">
              <v-img
                :src="require('@/assets/additional/asesoria/1.png')"
                align-center
                class="mb-6"
              ></v-img>
            </v-card>
       </div>
            <br />
            <br />
            <br />
            <v-card-text>
              <h3 class="headline mb-2 font-weight-bold">
                {{ selected.name }}
              </h3>
              <div class="subheading font-weight-bold text-justify">
                {{ selected.description }}
              </div>
            </v-card-text>
            <v-divider></v-divider>
          </v-card>
        </v-scroll-y-transition>
      </v-col>
    </v-row>
  </v-card>
</template>

<script>
export default {
  props: ["subservice"],
  data: () => ({
    active: [],
    avatar: null,
    open: [],
    // subservice: [],
  }),
  mounted() {},
  computed: {
    items() {
      return [
        {
          name: "Sub-servicios",
          children: this.subservice,
        },
      ];
    },
    selected() {
      if (!this.active.length) return undefined;

      const id = this.active[0];

      return this.subservice.find((user) => user.id === id);
    },
  },
};
</script>