<template>
  <v-form @submit.prevent="onSubmit">
    <v-row>
      <v-col md="12" cols="12">
        <v-text-field v-model="service.name" label="Nombre" required rows="3" />
      </v-col>
      <v-col md="12" cols="12">
        <v-textarea
          v-model="service.description"
          label="Descripción"
          outlined
          rows="3"
          placeholder="Escribe tu mensaje"
          icon="mdi-pencil"
          shaped
        ></v-textarea>
      </v-col>
      <v-col md="12" lg="12">
        <v-container v-for="(subserv, j) in service.optservices" :key="j">
          <v-card shaped outlined>
            <v-container>
              <v-row align="center" justify="center">
                <v-col lg="10" sm="10" md="10">
                  <v-card-text> Subservicio {{ j + 1 }} </v-card-text>
                </v-col>
                <v-col lg="2" sm="2" md="2">
                  <v-btn
                    class="mx-2"
                    fab
                    dark
                    x-small
                    outlined
                    color="red"
                    @click="removesubserv(j)"
                  >
                    <v-icon dark> mdi-close </v-icon>
                  </v-btn>
                </v-col>
              </v-row>
              <v-row align="center" justify="center">
                <v-col cols="12" lg="3" md="12" sm="12">
                  <v-card-text>
                    <v-text-field
                      v-model="subserv.name"
                      label="Nombre"
                      required
                      rows="3"
                    />
                  </v-card-text>
                </v-col>
                <v-col cols="12" lg="7" md="12" sm="12">
                  <v-card-text>
                    <v-textarea
                      v-model="subserv.description"
                      label="Descripción"
                      outlined
                      placeholder="Descripción"
                      icon="mdi-pencil"
                      shaped
                      rows="2"
                    ></v-textarea>
                  </v-card-text>
                </v-col>
                <v-col cols="12" lg="12" sm="12" md="12">
                  <v-file-input
                    v-model="subserv.file"
                    color="secondary"
                    counter
                    label="Subir imagen"
                    placeholder="Selecciona tu archivo"
                    prepend-icon="mdi-paperclip"
                    outlined
                    :show-size="1000"
                  >
                    <template v-slot:selection="{ index, text }">
                      <v-chip v-if="index < 2" color="secondary" dark label small>
                        {{ text }}
                      </v-chip>

                      <span
                        v-else-if="index === 2"
                        class="overline grey--text text--darken-3 mx-2"
                      >
                        +{{ subserv.file.length - 2 }} File(s)
                      </span>
                    </template>
                  </v-file-input>
                </v-col>
              </v-row>
            </v-container>
          </v-card>
        </v-container>
        <v-btn outlined color="primary" rounded @click="addsubserv()">
          <v-icon dark> mdi-plus </v-icon>

          Subservicio</v-btn
        >
      </v-col>
      <v-col md="12" cols="12" class="text-right">
        <slot></slot>
        <v-btn
          rounded
          color="primary"
          class="mx-md-1"
          type="submit"
          :disabled="loader"
        >
          <v-progress-circular
            class="mr-2"
            size="20"
            v-if="loader"
            indeterminate
            color="white"
          />
          {{ this.method.toLowerCase() === "post" ? "Agregar" : "Actualizar" }}
        </v-btn>
      </v-col>
    </v-row>
  </v-form>
</template>

<script>
export default {
  name: "ServiceForm",

  data: () => ({
    method: "POST",
    url: "/registrar-servicio",
    service: {
      name: "",
      description: "",
      optservices: [],
    },

    loader: false,
    error: null,
    activeActa: false,
    loaderActa: false,
  }),
  mounted() {},
  methods: {
    // request
    onSubmit() {
      if (!this.service.name) {
        this.$alert.err("Ingrese el nombre del servicio");
        return;
      }

      this.loader = true;
      this.$http({
        method: this.method,
        url: this.url,
        data: this.service,
      })
        .then((res) => {
          if (this.method.toLowerCase() === "post") {
            if (res.data && res.data.status === "success") {
              this.$emit("Service", res.data.service);
              this.$alert.ok("Servicio registrada con éxito");
            }
          } else {
            // on update user
            if (res.data && res.data.status && res.data.status === "success") {
              this.$emit("Service", res.data.Service);
              this.$alert.ok(res.data.message);
            }
          }
        })
        .catch((err) => {
          if (err.response && err.response.data) {
            this.$alert.err(err.response.data.message);
          }
        })
        .finally(() => {
          this.loader = false;
        });
    },
    addsubserv() {
      this.service.optservices.push({
        name: "Titulo",
        description: "Descripción",
      });
    },
    removesubserv(indexn) {
      this.service.optservices.splice(indexn, 1);
    },
    // extra functions
  },
};
</script>

<style scoped>
</style>
