<template>
  <v-dialog
    v-model="show"
    :fullscreen="$vuetify.breakpoint.smAndDown"
    width="600px"
    origin="center top"
  >
    <v-card>
      <v-card-title>
        <span>Editar Servicio</span>
        <v-spacer></v-spacer>
        <v-btn
          class="mx-2"
          fab
          dark
          small
          outlined
          color="black"
          @click="close()"
        >
          <v-icon dark> mdi-close </v-icon>
        </v-btn>
      </v-card-title>
      <v-card-text>
        <v-form @submit.prevent="onSubmit">
          <v-row>
            <v-col md="12" cols="12">
              <v-text-field
                v-model="form.name"
                label="Nombre"
                required
                rows="3"
              />
            </v-col>
            <v-col md="12" cols="12">
              <v-textarea
                v-model="form.description"
                label="Descripción"
                outlined
                rows="3"
                row-height="50"
                placeholder="Escribe tu mensaje"
                icon="mdi-pencil"
                shaped
              ></v-textarea>
            </v-col>
            <v-col md="12" lg="12">
              <v-container
                v-for="(subserv, j) in form.optservices"
                :key="subserv._id"
              >
                <v-card shaped outlined>
                  <v-container>
                    <v-row>
                      <v-col lg="10" sm="10" md="10">
                        <v-card-text> Subservicio {{ j + 1 }} </v-card-text>
                      </v-col>
                      <v-col lg="2" sm="2" md="2">
                        <v-btn
                          class="mx-2"
                          fab
                          dark
                          x-small
                          outlined
                          color="red"
                          @click="removesubserv(j)"
                        >
                          <v-icon dark> mdi-close </v-icon>
                        </v-btn>
                      </v-col>
                    </v-row>
                    <v-container>
                      <v-row align="center" justify="center">
                        <v-col cols="12" lg="12" sm="12" md="4">
                          <v-text-field
                            v-model="subserv.name"
                            label="Nombre"
                            required
                            rows="3"
                          />
                        </v-col>
                        <v-col cols="12" lg="12" sm="12" md="8">
                          <v-card-text>
                            <v-textarea
                              v-model="subserv.description"
                              label="Descripción"
                              outlined
                              placeholder="Descripción"
                              icon="mdi-pencil"
                              shaped
                              rows="2"
                            ></v-textarea>
                          </v-card-text>
                        </v-col>
                        <v-col cols="12" lg="12" sm="12" md="12">
                          <v-file-input
                            v-model="subserv.file"
                            color="secondary"
                            counter
                            label="Subir imagen"
                            placeholder="Selecciona tu archivo"
                            prepend-icon="mdi-paperclip"
                            outlined
                            :show-size="1000"
                          >
                            <template v-slot:selection="{ index, text }">
                              <v-chip
                                v-if="index < 2"
                                color="secondary"
                                dark
                                label
                                small
                              >
                                {{ text }}
                              </v-chip>

                              <span
                                v-else-if="index === 2"
                                class="overline grey--text text--darken-3 mx-2"
                              >
                                +{{ subserv.file.length - 2 }} File(s)
                              </span>
                            </template>
                          </v-file-input>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-container>
                </v-card>
              </v-container>
              <v-btn outlined color="primary" rounded @click="addsubserv()">
                <v-icon dark> mdi-plus </v-icon>
                Subservicio</v-btn
              >
            </v-col>

            <v-col md="12" cols="12" class="text-right">
              <slot></slot>
              <v-btn
                rounded
                color="primary"
                class="mx-md-1"
                type="submit"
                @click="setServices(form._id)"
                :disabled="loader"
              >
                <v-progress-circular
                  class="mr-2"
                  size="20"
                  v-if="loader"
                  indeterminate
                  color="white"
                />
                Actualizar
              </v-btn>
            </v-col>
          </v-row>
        </v-form>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "DlgEditService",
  props: {
    value: Boolean,
    form: {
      type: Object,
      default: function () {
        return {
          name: null,
          description: "",
          file:null,
          optforms: [],
        };
      },
    },
  },
  data: () => ({
    loader: false,
  }),
  methods: {
    setServices(id) {
      if (id === null) {
        return;
      }
      this.loader = true;
      this.$http({
        method: "put",
        url: `actualiza-servicio/${id}`,
        data: this.form,
      })
        .then((response) => {
          if (response.data.error) {
            this.$alert.err(`eorr` + response.data.error);
          } else {
            this.$alert.ok(`Servicio actualizado con éxito`);
            this.close();
          }
        })
        .finally(() => {
          this.loader = false;
        });
    },
    addsubserv() {
      this.form.optservices.push({
        name: "Titulo",
        description: "Descripción",
      });
    },
    removesubserv(indexn) {
      this.form.optservices.splice(indexn, 1);
    },
    close($event) {
      this.$emit("service", $event);
      this.show = false;
    },
  },
  computed: {
    show: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit("input", value);
      },
    },
  },
};
</script>

<style scoped>
</style>
